.nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    padding: 2em 2em;
    display: flex;
    align-self: center;
    gap: 2em;
    /* border-bottom: 1px solid rgb(178, 178, 178);
    background: var(--main-clr); */
}


/* Logo */
.logo{
    width: 2em;
    height: 2em;
    overflow: hidden;
}
.logo img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


/* Nav links */
.nav-links-wrapper{
    display: flex;
    align-items: center;
    gap: 1em;
}

.nav-links{
    position: relative;
    text-decoration: none;
    color: var(--font-clr);
    display: flex;
    align-items: center;
    font-size: 1.2rem;
}
.nav-links::before{
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -10px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--sec-clr);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: all 0.2s;
}
.nav-links:hover::before{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}


/* Nav linkes animated character */
.nav-links-character{
    position: relative;
    transition: transform 0.2s;
    color: var(--main-clr);
    overflow: hidden;
}

#character-K::before, #character-KK::before{
    content: "k";
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    color: black;
    transition: transform 0.2s;
}
#character-K::after, #character-KK::after{
    content: "k";
    position: absolute;
    bottom: -25px;
    left: 0;
    pointer-events: none;
    color: black;
    transition: transform 0.2s;
}
#character-E::before{
    content: "e";
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    color: black;
    transition: transform 0.2s;
}
#character-E::after{
    content: "e";
    position: absolute;
    bottom: -25px;
    left: 0;
    pointer-events: none;
    color: black;
    transition: transform 0.2s;
}
.nav-links:hover .nav-links-character::after{
    transform: translateY(-25px);
}
.nav-links:hover .nav-links-character::before{
    transform: translateY(-25px);
}



/* Sidebar open button */
.nav-sidebar-btn, .nav-sidebar-btn-active{
    position: fixed;
    right: 2em;
    top: 1em;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--sec-clr);
    cursor: pointer;
    transition: all 0.2s;
    z-index: 12;
}
.nav-sidebar-btn-active{
    transform: rotate(-90deg);
}
.nav-sidebar-btn:hover, .nav-sidebar-btn-active:hover{
    background: var(--sec-clr-dark);
}
.nav-sidebar-btn::before{
    content: "";
    position: absolute;
    left: 7px;
    top: 16px;
    width: 25px;
    height: 2px;
    background: var(--main-clr);
    transition: all 0.2s;
}
.nav-sidebar-btn:hover::before{
    top: 14px;
}
.nav-sidebar-btn::after{
    content: "";
    position: absolute;
    left: 7px;
    bottom: 16px;
    width: 25px;
    height: 2px;
    background: var(--main-clr);
    transition: all 0.2s;
}
.nav-sidebar-btn:hover::after{
    bottom: 14px;
}

.nav-sidebar-btn-active::before{
    content: "";
    position: absolute;
    left: 7px;
    top: 19px;
    width: 25px;
    height: 2px;
    background: var(--main-clr);
    transform: rotate(-45deg);
}
.nav-sidebar-btn-active::after{
    content: "";
    position: absolute;
    left: 7px;
    bottom: 19px;
    width: 25px;
    height: 2px;
    background: var(--main-clr);
    transform: rotate(45deg);
}