.sidebar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column-reverse;
    z-index: 11;
}


.sidebar-link{
    width: 100%;
    height: 20%;
    text-decoration: none;
    display: flex;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    transition: background 0.2s;
}
.sidebar-link p{
    font-size: 11rem;
    margin: 0;
    color: var(--font-clr);
    font-family: 'Bebas Neue', cursive;
}
#link-5{
    background: var(--main-clr);
}
#link-4{
    background: #4dff4d;
}
#link-3{
    background: #80ff80;
}
#link-2{
    background: #b3ffb3;
}
#link-1{
    background: #e5ffe5;
}
#link-4:hover, #link-3:hover, #link-2:hover, #link-1:hover{
    background: var(--main-clr);
}


.sidebar-socials{
    width: 100%;
    height: 20%;
    overflow: hidden;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 1em;
}
.sidebar-socials button{
    background: var(--font-clr);
    width: 2em;
    height: 2em;
    border-radius: 50%;
    border: none;
    color: var(--sec-clr);
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s;
}
.sidebar-socials button:hover{
    transform: scale(1.1);
}