:root{
  --font-clr: #111111;
  --font-light: #444444;
  --main-clr: #e0e0e0;
  --sec-clr: #37ff29;
  --sec-clr-dark: #c4cf15;
  --third-clr:#2317E8;
  --radius: 15px;
}

*{
  box-sizing: border-box;
}

html{
  padding: 0;
  margin: 0;
}
body{
  padding: 0;
  margin: 0;
  width: 100%;
  min-height: 100vh;
  font-family: 'Bebas Neue', cursive;
  /* font-family: 'Noto Serif Display', serif; */
  overflow-x: hidden;
}

body h1,h2,h3,h4,h5,h6{
  color: var(--font-clr);
}
body p,span,small{
  color: var(--font-light);
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Sen', sans-serif;
}

.canvas{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
}


/* Scrolling indicator */
.scroll-call{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 12.5%;
  bottom: 21%;
  color: var(--font-clr);
  z-index: 2;
}
.scroll-text{
  position: absolute;
  user-select: none;
  pointer-events: none;
  animation: Rotation 5s linear infinite;
}
@keyframes Rotation{
  0%{
      transform: rotate(-360deg);
  }
  100%{
      transform: rotate(0deg);
  }
}
.scroll-text span{
  font-size: 1.5rem;
  position: absolute;
  top:-100px;
  text-transform: uppercase;
  display: inline-block;
  transform-origin: 0 100px;
}
