.home-wrapper{
    width: 100%;
    min-height: 100vh;
}

.header-wrapper{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 1em;
    padding: 5em;
}
.header-wrapper-text{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
}

.header-title-stroke{
    margin: 0;
    font-size: 8rem;
    line-height: 0.9em;
    text-transform: capitalize;
    color: transparent;
    color: var(--font-clr);
}
.header-title-normal{
    margin: 0;
    font-size: 5rem;
    line-height: 0.9em;
    text-transform: capitalize;
    color: var(--font-clr);
}
.header-wrapper p{
    width: 50%;
    margin: 0;
    line-height: 1.5em;
}
.header-wrapper span{
    color: var(--font-clr);
    font-weight: 600;
}
.work-btn{
    background: var(--sec-clr);
    border: none;
    color: var(--font-clr);
    border-radius: 15px;
    padding: 1em 2em;
    cursor: pointer;
    transition: background 0.3s;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.work-btn:hover{
    background: var(--sec-clr-dark);
}
.header-wrapper-icon{
    width: 3em;
    height: 3em;
    background: var(--sec-clr);
    border-radius: 50%;
    border: 1px solid var(--font-clr);
    color: var(--font-clr);
}

.scroll-indicator{
    width: 3em;
    height: 3em;
    position: absolute;
    right: 5%;
    bottom: 5%;
    font-size: 4rem;
    border-radius: 50%;
    background: #11111155;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Section intro */
.section-intro{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
}
.section-intro-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.section-intro-content h1{
    margin: 0;
    font-size: 12rem;
    line-height: 0.8em;
    transform: translateY(300px);
    opacity: 0;
}
.section-intro-content p{
    width: 100%;
    margin: 0;
    text-align: left;
    line-height: 1.5em;
    transform: translateY(300px);
    opacity: 0;
}


/* Section project single */
.section-project{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1em;
}


.section-project-img{
    position: relative;
    width: 60%;
    height: 70%;
    border-radius: 25px 0 0 25px;
    overflow: hidden;
}
.section-project-overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
}
.section-project-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.section-project-img-text{
    position: absolute;
    bottom: 2em;
    right: 2em;
    width: 40%;
    background: white;
    border-radius: 25px;
    padding: 1em;
}
.section-project-img-text p{
    width: 100%;
    line-height: 1.5em;
}
.section-project-btns{
    position: absolute;
    left: 2em;
    bottom: 2em;
    display: flex;
    align-items: center;
    gap: 1em;
}
.section-project-btns button{
    border: 1px solid var(--sec-clr);
    background: var(--main-clr);
    color: var(--sec-clr);
    width: 3em;
    height: 3em;
    border-radius: 50%;
    font-size: 1.2rem;
}
.section-project-btns-tags{
    padding: 1em 2em;
    border-radius:35px;
    background: var(--sec-clr);
    color: var(--font-light);
}


.section-project-text{
    width: 25%;
    height: 70%;
    background: var(--main-clr);
    border: 1px solid var(--font-light);
    border-radius: 0 35px 35px 0;
}


/* Section projects */
.section-projects{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.section-projects-links{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3em;
    padding: 5em;
}


.section-projects-link, .section-projects-link-active{
    width: 40%;
    position: relative;
    text-decoration: none;
}
.section-projects-link h1{
    width: 100%;
    width: max-content;
    margin: 0;
    font-size: 3.5rem;
    text-transform: capitalize;
    color: var(--font-clr);
}
.section-projects-link-active h1{
    text-decoration: none;
    width: max-content;
    margin: 0;
    font-size: 3.5rem;
    text-transform: capitalize;
    color: var(--font-clr);
}
.section-projects-link span{
    opacity: 0;
    color: var(--sec-clr);
    font-size: 1.5rem;
    position: absolute;
    left: -50px;
    top: 8px;
}

.section-projects-link-open-btn{
    width: max-content;
    text-decoration: none;
    border: 1px solid var(--font-clr);
    color: var(--font-clr);
    border-radius: 35px;
    padding: 0.5em 1em;
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    cursor: pointer;
    transition: all 0.2s;
}
.section-projects-link-open-btn:hover{
    background: white;
}
.section-projects-link-open-btn:hover .section-projects-link-icon{
    transform: scale(1.2) translateX(30px);
    background: white;
    border-color: var(--font-clr);
}
.section-projects-link-icon{
    border: 1px solid var(--font-clr);
    border-radius: 50%;
    width: 3em;
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
}




.section-project-info{
    width: 30%;
}
.section-project-info p{
    width: 100%;
}