footer{
    width: 100%;
    height: 90vh;
    background: #000000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.footer-top{
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2em;
}
.footer-top h1{
    font-size: 5rem;
    color: white;
    margin: 0;
}
.footer-top p{
    color: white;
    margin: 0;
}



.footer-bottom{
    width: 100%;
    border-top: 1px solid white;
    padding: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.copyright{
    display: flex;
    align-items: center;
    gap: 0.5em;
    color: grey;
    font-size: 0.7rem;
}
.copyright p{
    width: max-content;
    margin: 0;
    color: grey;
    font-size: 0.7rem;
}
.copyright span{
    color: white;
    font-size: 0.7rem;
}

.footer-bottom-socials{
    display: flex;
    align-items: center;
    gap: 0.5em;
    color: grey;
}
.footer-bottom-socials button{
    background: transparent;
    width: max-content;
    height: max-content;
    border: none;
    color: white;
}



.about-contact-btn{
    text-decoration: none;
    border: 1px solid white;
    color: white;
    border-radius: 35px;
    padding: 0.5em 1em;
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    cursor: pointer;
    transition: all 0.2s;
}
.about-contact-btn:hover{
    background: white;
}
.about-contact-btn:hover .about-icon{
    transform: scale(1.2) translateX(30px);
    background: white;
    border-color: var(--main-clr);
}
.about-icon{
    border: 1px solid white;
    border-radius: 50%;
    width: 3em;
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
}
