.contact-wrapper{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
    padding: 10em 0;
    background: #ffffff95;
}
.contact-wrapper h1{
    width: 50%;
    font-size: 5rem;
    margin: 0;
    color: var(--font-clr);
}
.contact-wrapper p{
    width: 50%;
    margin: 0;
    color: var(--font-light);
}


#contact-form{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-top: 2em;
}
#contact-form label{
    color: var(--font-clr);
}
#contact-form input{
    background: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid var(--font-light);
    font-size: 1.2rem;
    padding: 1em;
    color: var(--font-light);
}
#contact-form input:focus{
    border-bottom: 3px solid var(--sec-clr);
}
#contact-form textarea{
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 2px solid var(--font-light);
    font-size: 1.2rem;
    padding: 1em;
    color: var(--font-light);
    font-family: 'Roboto', sans-serif;
    resize: none;
}
#contact-form button{
    width: 100%;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--sec-clr);
    padding: 2em;
}
.contact-form-icon{
}

#contact-form div{
    display: flex;
    align-items: flex-start;
    gap: 0.5em;
    color: var(--font-light);
}