.about-wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.about-text{
    padding: 5em 2em;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.about-header{
    font-size: 5rem;
}
.about-first-p, .about-second-p{
    color: var(--font-light);
    line-height: 1.5em;

}